<a name="process55"></a>

<p><br></p>
<p><span class="rvts58">火元素学派（</span><span class="rvts59">Pyrokinetic</span><span class="rvts58">）：</span>
<br><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber193.jpg"><span class="rvts28"></span></p>
<p><span class="rvts21">火元素学派技能一共有</span><span class="rvts20">24</span><span class="rvts21">个。所有的火元素学派技能都基于智力（</span><span class="rvts20">Intelligence</span><span class="rvts21">）属性造成额外的火元素伤害加成。火元素学派有很多可以对敌人造成</span><span class="rvts20">AOE</span><span class="rvts21">（范围）伤害的技能。</span>
<br><span class="rvts21">火元素技能伤害多为爆发型，被火元素攻击的单位会被点燃，并在接下来的战斗时间内受到持续的燃烧伤害。</span>
<br><span class="rvts43"></span>
<br><span class="rvts29">火元素学派技能书可以从以下</span><span class="rvts33">NPC</span><span class="rvts29">中买到（或学到）：</span>
<br><span class="rvts20">1. </span><span class="rvts21">斯汀泰尔（</span><span class="rvts20">Stingtail</span><span class="rvts21">）</span><span class="rvts20"> --牋</span><span class="rvts21">欢乐堡外</span>
<br><span class="rvts20">2. </span><span class="rvts21">萨玛黛儿（</span><span class="rvts20">Samadel</span><span class="rvts21">）</span><span class="rvts20"> --</span><span class="rvts21">阿玛迪亚避难所</span>
<br><span class="rvts20">3.</span><span class="rvts21">阿尔麦拉（</span><span class="rvts20">Almira</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">复仇女士号</span>
<br><span class="rvts20">4.</span><span class="rvts21">欧维斯（</span><span class="rvts20">Ovis</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">浮木城，死神海岸</span>
<br><span class="rvts20">5. </span><span class="rvts21">拖瓦（</span><span class="rvts20">Tovah</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">精灵营地，死神海岸</span>
<br><span class="rvts20">6.</span><span class="rvts21">阿尔麦拉（</span><span class="rvts20">Almira</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">复仇女士号</span>
<br><span class="rvts20">7. </span><span class="rvts21">黑环军需官（</span><span class="rvts20">Black Ring Quartermaster</span><span class="rvts21">）</span><span class="rvts20"> --</span><span class="rvts21">费勒斯神庙南部，无名岛</span>
<br><span class="rvts20">8. </span><span class="rvts21">武僧蜥蜴（</span><span class="rvts20">Lizard Monk</span><span class="rvts21">）</span><span class="rvts20"> --</span><span class="rvts21">泰森地留斯神庙，无名岛</span>
<br><span class="rvts20">9.</span><span class="rvts21">商人弗利尔（</span><span class="rvts20">Trader Friel</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
